p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

img {
  width: 260px;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  display: block;
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  background-color: #ffebcd;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.search-form-input {
  border: none;
  outline: none;
  padding-left: 15px;
}

.gallery {
  flex-wrap: wrap;
  display: flex;
}

.lightbox {
  color: #000;
  text-decoration: none;
}

.photo-card {
  margin-top: 20px;
  margin-left: 30px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 2px rgba(0, 0, 0, .24);
}

.info {
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  font-size: 12px;
  display: flex;
}

.info-number {
  margin-top: 5px;
  font-size: 11px;
  font-weight: 400;
}

